@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap");

:root {
  --font-family: "Poppins", sans-serif;
  --dark-blue: #0a1930;
  --light-blue: #1f93ff;

  --color-white: #fff;
  --color-dark: #333;

  --color-grey: #eee;
  --box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);

  --color-purple: #9d0191;
  --color-orange: #ff7722;

  --color-primary: #007bff;
  --color-success: #28a745;
}

* {
  margin: 0;
  padding: 0;
  /* text-decoration: none; */
  list-style: none;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

html {
  font-size: 10px;
}

body {
  font-family: var(--font-family);
}

section {
  width: 100%;
  padding: 8rem 0;
}

.container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 20px;
}

/* UTILITY CLASSES */

/* Flex */

.--flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}
.--flex-start {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}
.--flex-end {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.--flex-between {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.--align-center{
  align-items: center;
}

.--dir-column {
  display: flex;
  flex-direction: column;
}

.--cg{
  column-gap: 5px;
}

.--cg1{
  column-gap: 10px;
}

.--rg{
  row-gap: 5px;
}

.--rg1{
  row-gap: 10px;
}


.--f-wrap{
  flex-wrap: wrap;
}

/* DISPLAYS */

.--d-flex {
  display: flex;
}

.--d-block {
  display: block;
}

.--d-none{
  display: none;
}

/* HEIGHTS */

.--100vh {
  height: 100vh;
}

.--h100{
  height: 100%;
}
.--h50{
  height: 50%;
}

.--mh-100vh {
  min-height: 100vh;
}

/* WIDTHS */

.--w50{
  width: 50%;
}

.--w100{
  width: 100%;
}


/* Grid */

.--grid-15 {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));
  row-gap: 1rem;
  column-gap: 1rem;
}
.--grid-25 {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(24rem, 1fr));
  row-gap: 1rem;
  column-gap: 1rem;
}

/* Center All */

.--center-all {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  margin: auto;
  text-align: center;
}

/* Heading */
h1,
h2,
h3,
h4 {
  font-weight: 500;
  line-height: 1.2;
  color: var(--color-dark);
  margin-bottom: 1rem;
}
h1 {
  font-size: 4rem;
}
h2 {
  font-size: 3rem;
}
h3 {
  font-size: 2.5rem;
}
h4 {
  font-size: 2rem;
}

p {
  font-size: 1.5rem;
  font-weight: 300;
  line-height: 1.3;
  color: var(--color-dark);
}

/* Font size */

.--text-xxl {
  font-size: 4.5rem;
}

.--text-xl {
  font-size: 4rem;
}

.--text-lg {
  font-size: 3.2rem;
}

.--text-md {
  font-size: 2.6rem;
}

.--text-sm {
  font-size: 2rem;
}

.--text-base {
  font-size: 1.6rem;
}

.--text-xs {
  font-size: 1.4rem;
  font-weight: 300;
}
.--text-p {
  font-size: 1.5rem;
  font-weight: 300;
  line-height: 1.3;
  color: var(--color-dark);
}


/* FONT WEIGHT */

.--fw-bold {
  font-weight: 600;
}

.--fw-base{
  font-weight: 400;
}

.--fw-thin {
  font-weight: 200;
}

/* Border radius */

.--rd-10{
  border-radius: 10px;
}
.--rd-8{
  border-radius: 8px;
}
.--rd-6{
  border-radius: 6px;
}
.--rd-4{
  border-radius: 4px;
}
.--rounded{
  border-radius: 50%;
}

/* Text Color */

.--text-light {
  color: #fff;
}

.--color-primary {
  color: #007bff;
}
.--color-danger {
  color: orangered;
}
.--color-success {
  color: #28a745;
}

.--color-white {
  color: #fff;
}

/* Center Text */

.--text-center {
  text-align: center;
}

/* Card */

.--card {
  border: 1px solid transparent;
  border-radius: 5px;
  box-shadow: var(--box-shadow);
  overflow: hidden;
}


/* CURSOR */

.--pointer{
  cursor: pointer;
}



/* Margin */

.--m {
  margin: 1rem;
}

.--ml {
  margin-left: 1rem;
}

.--mr {
  margin-right: 1rem;
}

.--mb {
  margin-bottom: 1rem;
}

.--my {
  margin: 1rem 0;
}
.--mx {
  margin: 0 1rem;
}

.--m2 {
  margin: 2rem;
}

.--mt2 {
  margin-top: 2rem;
}

.--ml2 {
  margin-left: 2rem;
}
.--mr2 {
  margin-right: 2rem;
}

.--mb2 {
  margin-bottom: 2rem;
}

.--my2 {
  margin: 2rem 0;
}

.--mx2 {
  margin: 0 2rem;
}

.--mr-auto{
  margin-right: auto;
} 

.--ml-auto{
  margin-left: auto;
}

/* Padding */

.--p {
  padding: 1rem;
}
.--py {
  padding: 1rem 0;
}
.--px {
  padding: 0 1rem;
}
.--pl{
  padding-left: 1rem;
}
.--pr{
  padding-right: 1rem;
}

.--p2 {
  padding: 2rem;
}
.--py2 {
  padding: 2rem 0;
}
.--px2 {
  padding: 0 2rem;
}
.--pl2{
  padding-left: 2rem;
}
.--pr2{
  padding-left: 2rem;
}



/* BUTTON */

.--btn {
  font-size: 1.5rem;
  font-weight: 400;
  padding: 4px 8px;
  margin-bottom: 1rem;
  border: 1px solid transparent;
  border-radius: 4px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s;
}

.--btn:hover {
  transform: translateY(-2px);
}

.--btn-lg {
  padding: 8px 10px;
}

.--btn-block {
  width: 100%;
}

.--btn-primary {
  color: #fff;
  background: #007bff;
}
.--btn-secondary {
  color: #fff;
  border: 1px solid #fff;
  background: transparent;
}
.--btn-danger {
  color: #fff;
  background: orangered;
}

.--btn-success {
  color: #fff;
  background: #28a745;
}

/* Background */

.--bg-white {
  background: #fff;
}

.--bg-dark {
  background: var(--color-dark);
}

.--bg-primary {
  background: var(--color-primary);
}

.--bg-success {
  background: var(--color-success);
}

.--bg-grey {
  background: var(--color-grey);
}


/* FORM */

.--form-control {
  font-size: 1.6rem;
  font-weight: 300;
}

.--form-control > * {
  margin: 5px 0;
}

.--form-control input {
  font-size: 1.6rem;
  font-weight: 300;
  padding: 8px 1rem;
  border: 1px solid #777;
  border-radius: 3px;
  outline: none;
}
.--form-control select {
  font-size: 1.4rem;
  font-weight: 400;
  padding: 8px 1rem;
  border: 1px solid #777;
  border-radius: 3px;
}

.--form-control label {
  font-size: 1.6rem;
  font-weight: 400;
  display: inline-block;
  min-width: 7rem;
  color: var(--color-dark);
  margin-right: 1rem;
}

@media screen and (max-width: 600px) {
  .--flex-dir-column {
    flex-direction: column;
  }
}

.--block {
  display: block;
}
.--inline-block {
  display: inline-block;
}

.--width-100 {
  width: 100%;
}
.--width-500px {
  width: 500px;
}

.--line {
  position: relative;
}

.--line::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -5px;
  transform: translateX(-50%);
  width: 5rem;
  height: 3px;
  margin-bottom: 1rem;

  background: rgb(217, 8, 170);
  background: linear-gradient(
    135deg,
    rgba(163, 1, 191, 1) 44%,
    rgba(217, 8, 170, 1) 57%
  );
}

.--list-style-none {
  list-style: none;
}

.--profile-img {
  width: 6rem;
  height: 6rem;
  border: 1px solid #ccc;
  border-radius: 50%;
}

a {
  font-size: 1.4rem;
  color: var(--dark-blue);
  text-decoration: none;
  transition: all 0.2s;
}

a:hover {
  color: var(--color-dark);
  font-size: 1.5rem;
}
